<template>
  <div><h1>商品下架</h1></div>
  <el-table :data="datas" stripe style="width: 100%">
    <el-table-column prop="provider" label="主理人ID" />
    <el-table-column prop="id" label="商品ID" />

    <el-table-column prop="full_name" label="商品名称" />
    <el-table-column
      prop="activity_start_time"
      label="活动开始时间"
      width="180" />
    <el-table-column prop="activity_end_time" label="活动结束时间" />
    <el-table-column label="Operations">
      <template #default="scope">
        <el-button type="danger" @click="handleDelete(scope.$index, scope.row)"
          >下架</el-button
        >
      </template>
    </el-table-column>
  </el-table>
</template>

<script setup>
import { onMounted, ref } from 'vue'
import { ElMessageBox, ElMessage } from 'element-plus'
import axios from 'axios'
const request = axios.create({
  auth: {
    username:
      'NginxAuthUsername16381efa069a90cbde99854c81183036a4bd2e4776eb770fa69740f1c5c06aa4',
    password:
      'NginxAuthPassworde0588e3b3c5b240fe3f5fb653d2990f34b52ee0b595acf6553a924b0716cb366'
  }
})
const datas = ref([
  {
    provider: 'ID',
    activity_start_time: '1',
    activity_end_time: '2',
    full_name: 'name'
  }
])
const handleDelete = (index, p) => {
  ElMessageBox.confirm('确定删除商品吗?', 'Warning', {
    confirmButtonText: '确认',
    cancelButtonText: '取消',
    type: 'warning'
  }).then(() => {
    request
      .post('/api/e9b849a515a84327b424af7ccdbf2949/v1_0_0/product/delete', {
        id: p.id
      })
      .then(r => {
        if (r.data.Code == '200') {
          getProducts()
          ElMessage({
            type: 'success',
            message: 'Delete completed'
          })
        }
      })
  })
}
const getProducts = () => {
  request
    .post('/api/e9b849a515a84327b424af7ccdbf2949/v1_0_0/product/all')
    .then(r => {
      datas.value = r.data.details.products.filter(p => {
        return p.status == 5400
      })
    })
}
onMounted(() => {
  getProducts()
})
</script>
<style scoped></style>
